
import React from "react";
import Github from "../components/Github";
import logo from "../Images/img.png";
import spa from "../Images/logo1.png"

function Apropos() {
  return (
    <section className="container py-5">
      <div className="container text-center">
        <div className="row">
          
          <div className="col-md-5">
           <h2 className="text-dark"> propos de </h2>
            <img src={spa} alt="img spa" style={{ height: "80px" }}/>
            <p className="text-muted">WEBIW est un projet personnel de dveloppement web.
              Ce site me sert de laboratoire pour exprimenter, apprendre
              et construire des applications web compltes.
              Aprs avoir travaill le backend et le frontend sparment,
              jai dcid de les runir dans un mme projet afin de mieux
              comprendre larchitecture globale dune application web.
              WEBIW volue au fil de mes apprentissages.
              Chaque fonctionnalit est une opportunit damliorer mes
              comptences techniques et ma comprhension du dveloppement full-stack.
              Ce projet nest pas une agence, mais un espace dexprimentation,
              de progression et de partage autour du dveloppement web.
            </p>
          </div>

        <div className="col-md-7">
          <img src={logo} alt="img logo" className="mt-5" style={{ height: "250px" }}/>
        </div>
        </div>

        <div className="row">
          <div className="col-md-5">
            <h3 className="fw-bold mb-3 text-dark">Un projet dapprentissage concret</h3>
            <p className="text-muted">
              Jai dj travaill le backend et le frontend sparment. <br />
              Aujourdhui, je les runis dans un mme projet pour mieux
              comprendre <br />
              larchitecture complte dune application web.
            </p>
          </div>
        <div className="col-md-7">
          <Github/>
        </div>
      </div>
      </div> 
    </section>
  );
}

export default Apropos;
